import dayjs from "dayjs";

export const getLastViewTimeString = (datetime: string) => {
  const diffTimestamp = Math.abs(dayjs(datetime).diff(new Date(), "date"));
  // 天
  if (diffTimestamp > 60 * 60 * 24) {
    return dayjs(diffTimestamp).format("DD") + " days";
  }
  // 時
  if (diffTimestamp > 60 * 60) {
    return dayjs(diffTimestamp).format("HH") + " hours";
  }
  // 分
  if (diffTimestamp > 60) {
    return dayjs(diffTimestamp).format("MM") + " minutes";
  }
  // 秒
  return dayjs(diffTimestamp).format("SS") + " seconds";
};
