import request from "@/apis/http";
import { AxiosPromise } from "axios";
import {
  FabricDetailResponse,
  FabricFiltersResponse,
  FabricsParams,
  FabricsResponse,
  FabricRelatesResponse,
  StatusChangeParams,
  FabricRelatedCollectionResponse
} from "@/typings/fabric";
import { Message } from "@/typings/common";

export function getFabrics(data: FabricsParams): AxiosPromise<FabricsResponse> {
  return request({
    url: "/fabrics/",
    method: "GET",
    params: data
  });
}

export function getFabric(id: string): AxiosPromise<FabricDetailResponse> {
  return request({
    url: `/fabric/${id}/`,
    method: "GET"
  });
}

export function getFabricFilters(): AxiosPromise<FabricFiltersResponse> {
  return request({
    url: "/filter/fabric/",
    method: "GET"
  });
}

export function postFabricStatus({ id, status }: StatusChangeParams) {
  return request({
    url: `/fabric/${id}/status/`,
    method: "PUT",
    data: { status }
  });
}

export function getFabricRelates(
  id: string
): AxiosPromise<FabricRelatesResponse> {
  return request({
    url: `/fabric/${id}/related/`,
    method: "GET"
  });
}

export function getFabricRelatedCollections(
  id: string
): AxiosPromise<FabricRelatedCollectionResponse[]> {
  return request({
    url: `/fabric/${id}/related_collection/`,
    method: "GET"
  });
}

export function postAddFabricToFavorite(id: string): AxiosPromise<Message> {
  return request({
    url: `/fabric/${id}/add_to_favorite/`,
    method: "PUT"
  });
}

export function getFabricAnalyzeRecentView() {
 return request({
   url: `http://140.112.110.129:7777/api/analyze_logs/fabrics/recent_view`,
   method: 'GET'
 })
}

export function getFabricAnalyzeCompanyRank() {
  return request({
    url: `http://140.112.110.129:7777/api/analyze_logs/fabrics/company_rank`,
    method: 'GET'
  })
}

export function getFabricAnalyzeObjectRank() {
  return request({
    url: `http://140.112.110.129:7777/api/analyze_logs/fabrics/object_rank`,
    method: 'GET'
  })
}
