import { Router } from "vue-router";
import { findToken } from "@/utils";

const whiteList = ["/login", "/invite-confirm"]; // no redirect whitelist

export function installPermission(router: Router) {
  router.beforeEach((to, from, next) => {
    if (findToken()) {
      /* has token*/
      if (whiteList.includes(to.path)) {
        next("/brand-insights/brand-summary");
      } else {
        next();
      }
    } else {
      /* has no token*/
      if (whiteList.includes(to.path)) {
        // 在免登入白名單，直接進入
        next();
      } else {
        next("/login"); // 否則全部重新定向到登入頁
      }
    }
  });
}
